export default function ListCtrl() {
  var vm = this;
  vm.list = [
    { name: '', key: 'platform', width: '10%' },
    { name: '', key: 'company', width: '10%' },
    { name: '', key: 'profile', width: '10%' },
    { name: '', key: 'trade', width: '10%' },
    { name: '', key: 'introducer', width: '10%' },
    { name: '', key: 'contacts', width: '10%' },
    { name: '', key: 'email', width: '10%' },
    { name: '', key: 'plan', width: '20%', link: true, linkkey: 'plan' },
    { name: '', key: 'state', width: '10%' },
  ];
}



